import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Acao_preventiva } from '../../../modelo/Acao_preventiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva-dados-details',   templateUrl: 'acao_preventiva_dadosDetails.component.html' }) export class Acao_preventivaDadosDetailsComponent implements OnInit {    acao_preventiva: Acao_preventiva;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.acao_preventiva.Acao_preventiva";                 let id = +params['id'];         this.acao_preventiva = new Acao_preventiva();         this.acao_preventiva.cd_acao_corretiva = id;         o.objetoJson = this.acao_preventiva;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a acao_preventiva. '; }      this.acao_preventiva = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


